<template>
  <li class="profile-nav onhover-dropdown pe-0 py-0">
    <div class="media profile-media">
      <img class="b-r-10" src="@/assets/images/dashboard/profile.png" alt="" />
      <div class="media-body">
        <span>{{ user.name }}</span>
        <p class="mb-0 font-roboto">
          {{ user.role_name }} <i class="middle fa fa-angle-down"></i>
        </p>
      </div>
    </div>
    <ul class="profile-dropdown onhover-show-div">
      <li>
        <vue-feather type="user"></vue-feather><span>Account</span>
      </li>
      <li @click="logout" style="cursor: pointer">
        <vue-feather type="log-in"></vue-feather><span>Log out</span>
      </li>
    </ul>
  </li>
</template>

<script>
// 1. WAJIB: Ganti 'axios' dengan 'apiClient' yang sudah dikonfigurasi
import apiClient from '@/services/users'; // <-- Sesuaikan path ini jika perlu

export default {
  name: 'Profile',
  data() {
    return {
      user: {
        name: 'Pengguna',     // Nilai default untuk mencegah error tampilan
        role_name: 'Role' // Nilai default
      }
    };
  },
  mounted() {
    this.fetchUserProfile();
  },
  methods: {
    fetchUserProfile() {
      const userData = JSON.parse(localStorage.getItem('User')) || {};
      const userId = userData.id_pengguna;

      if (!userId) {
        // Jika tidak ada user ID, tidak perlu panggil API
        return;
      }

      // 2. GUNAKAN apiClient: baseURL dan header token ditambahkan otomatis
      // Path di sini kosong ('') karena kita hanya ingin menambahkan query params ke baseURL
      apiClient.get('', { 
        params: { 
          act: 'users',
          key: userId
        }
      })
      .then(res => {
        // 3. SESUAIKAN DENGAN STRUKTUR JSON BARU ANDA
        const userInfo = res.data?.data?.[0]; // <-- Mengambil objek pertama dari array 'data'
        
        if (userInfo) {
          this.user.name = userInfo.name || 'Pengguna';
          this.user.role_name = userInfo.roles?.role_name || 'Role';
        }
      })
      .catch(err => {
        console.error('Gagal mengambil data user:', err);
        // Jika token tidak valid (error 401/403), paksa logout
        if (err.response && (err.response.status === 401 || err.response.status === 403)) {
          this.logout();
        }
      });
    },

    logout() {
      // 4. GUNAKAN apiClient untuk logout: URL dan token sudah otomatis
      apiClient.get('/logout')
        .then(response => {
          console.log('Logout dari server berhasil.', response.data);
          // Hanya jika server berhasil, bersihkan data di sisi klien
          localStorage.removeItem('User');
          this.$router.replace('/auth');
        })
        .catch(error => {
          console.error('API logout gagal:', error);
          // Jika API gagal, tetap log out dari sisi klien agar pengguna tidak terjebak
          alert('Sesi di server mungkin masih aktif, namun Anda akan dikeluarkan dari aplikasi.');
          localStorage.removeItem('User');
          this.$router.replace('/auth');
        });
    }
  }
};
</script>